import Paper from '@material-ui/core/Paper';
//import {Ticket} from './model'
import LocationSearchInput from '../profilepage/LocationSearchInput';
//@ts-ignore
//import DateTimePicker from 'react-datetime-picker';
import {stripAddress} from '../profilepage/util';
import { GeocoderResult} from '../profilepage/model';
import AppHomeLayout from '../app/AppHomeLayout'
//import TextareaAutosize from '@material-ui/core/TextareaAutosize';
import { API, graphqlOperation } from 'aws-amplify'
import { createTicket } from '../graphql/mutations'
import { useHistory } from 'react-router-dom'
// @ts-ignore
import { uuid } from 'uuidv4';
import { AuthContext } from '../auth/auth-context'
import ImageUploader from 'react-images-upload';

import {handleImageUpload} from '../utils/S3Handler';

const useStyles = makeStyles(theme => ({
  appBar: {
    position: 'relative',
  },
  layout: {
    width: 'auto',
    //marginLeft: theme.spacing(2),
    //marginRight: theme.spacing(2),
    [theme.breakpoints.up(600 + theme.spacing(2) * 2)]: {
      width: 600,
      marginLeft: 'auto',
      marginRight: 'auto',
    },
  },
  paper: {
    marginTop: theme.spacing(3),
    marginBottom: theme.spacing(3),
    padding: theme.spacing(2),
    [theme.breakpoints.up(600 + theme.spacing(3) * 2)]: {
      marginTop: theme.spacing(6),
      marginBottom: theme.spacing(6),
      padding: theme.spacing(3),
    },
  },
  buttons: {
    display: 'flex',
    justifyContent: 'flex-end',
  },
  button: {
    marginTop: theme.spacing(3),
    marginLeft: theme.spacing(1),
  },
}));

export interface AddressFormProps {
  nextButton: (value: string) => void
}

  const CreateTicket: React.SFC<AddressFormProps> = (props) => {

    const classes = useStyles();

  const handleInputChange = (e: any) => {
    const {name, value} = e.target
    if(name == "price"){
      const re = /^([1-9]{0,2}((.)[0-9]{0,2}))$/;
      if (e.target.value === '' || re.test(e.target.value)) {
        setValues({...values, [name]: value})
      }
    }else {
      setValues({...values, [name]: value})

    }
}
const history = useHistory()
const authContext = React.useContext(AuthContext)


const createNewTicket = async() =>{
  console.log("createNewTicket", values);
  const id = authContext.userId;

  const phone_number = authContext.user.attributes["phone_number"];
  const email = authContext.user.attributes["email"];
  const ticketUserInfo = JSON.stringify({
    email: email,
    phoneNumber: phone_number
  });
  let S3path: any[] = [];
  if(pictures.length > 0){
     S3path = await handleImageUpload(pictures); 
  }

  const newId= uuid();
  const county = "Davidson County";

  const response =  await API.graphql(
    graphqlOperation(createTicket, { input: {
      id: newId,
      countAndId: `${county}_${newId}`,
      price : values.price,
      time : values.time,
      title : values.title,
      content:  values.content ,
      destination : values.destination?? "Davidson County" ,
      source : values.source ?? "Davidson County" ,
      county: county,
      ownerUploadImages : JSON.stringify(S3path),
      ticketUserId: id,
      status: 'open',
      ticketUserInfo: ticketUserInfo
    } })
);
console.log("response", response);

history.push('/')

}
const sourceGoogle = (result: GeocoderResult[]) => {
  console.log("address",result);
  const value = stripAddress(result);
  setValues({...values, source: value.formatted_address})
};

const destGoogle = (result: GeocoderResult[]) => {
  console.log("address",result);
  const value = stripAddress(result);
  setValues({...values, destination: value.formatted_address, county: value.county})
};

 const [values, setValues] = useState({ content: '',price: '', title: '',
 source: '', destination: '',time: "", county: ''})

  const [disable, setDisable] = useState<boolean>(true)

  useEffect(() => {
    const {content, title, price,  time} = values
    setDisable(!(content &&  title && price  &&  time))
  }, [values ]);

  const onChange = (date: any) => {
   setValues({...values, ["time"]: date})
  };
  

  // image upload
  const [pictures, setPictures] = useState<any[]>([]);
  const [key, setKey] = useState<any>({});

  const onDrop = async (picture: any) => {
    setPictures(picture);
  };

  return (
    <React.Fragment>
          <AppHomeLayout title='RA2 Serverless'>
          <CssBaseline />
      <main className={classes.layout}>
        <Paper className={classes.paper}>

      <Typography variant="h6" gutterBottom >
      Job Details
      </Typography>
      <Grid container spacing={1}>
      <Grid item xs={12} sm={6}>
          <TextField
            required
            id="title"
            name="title"
            label="Title"
            fullWidth
            onChange={handleInputChange}
            value={values.title}
            autoComplete="title"
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextField
            required
            id="price"
            name="price"
            label="Price"
            fullWidth
            onChange={handleInputChange}
            value={values.price}
            autoComplete="price"
          />
        </Grid>

        <Grid item xs={12} sm={6}>
          <LocationSearchInput label = "source" googleAddressReturn={sourceGoogle}
         />
          </Grid>

          <Grid item xs={12} sm={6}>
          <LocationSearchInput label = "destination" googleAddressReturn={destGoogle}
         />
          </Grid>

        <Grid item xs={12}>
        {/* <DateTimePicker
           name= "Job Date"
          onChange={onChange}
          value={values.time}
        /> */}
         <TextField
          required
          id="time"
          name="time"
          label="time"
          type="datetime-local"
          //defaultValue="2017-05-24T10:30"
          fullWidth
          onChange={handleInputChange}
          value={values.time}
    InputLabelProps={{
      shrink: true,
    }}
  />
        </Grid>
        <Grid item xs={12}>
        <TextField
          multiline
          rows="6"
          required
          id="content"
          name="content"
          label="content"
          fullWidth
          onChange={handleInputChange}
          value={values.content}
          autoComplete="content"
        />
        </Grid>

        <Grid item xs={12}>
        <ImageUploader
      withPreview={true}
      withIcon={true}
      onChange={onDrop}
      imgExtension={[".jpg", ".gif", ".png", ".gif"]}
      maxFileSize={5242880}
    />
    
            </Grid>
        <Grid
  container
  direction="row"
  justify="flex-end"
  alignItems="center"
>

<Button disabled= {disable} variant="contained"
  color="primary"
  className={classes.button}
onClick={createNewTicket}>Create Ticket</Button>

</Grid>
      </Grid>

      </Paper>
      </main>

      </AppHomeLayout>
    </React.Fragment>
  );
}

export default CreateTicket
